import React from "react";
import ExpenxeForm from "./ExpenxeForm";
import "./NewExpenxes.css";

function NewExpenxes(props) {
  const saveExpenxe = (expen) => {
    const expression = {
      ...expen,
      id: Math.random().toString()
    };
    props.addExpenxe(expression);
  };
  return (
    <div className="new-expense">
      <ExpenxeForm onSaveExpenxeDate={saveExpenxe} />
    </div>
  );
}

export default NewExpenxes;
