import React, { useState } from "react";
import "./ExpenxeForm.css";

function ExpenxeForm(props) {
  const [title, setTitle] = useState();
  const [amount, setAmount] = useState();
  const [date, setDate] = useState();

  const changeTitle = (event) => {
    setTitle(event.target.this);
  };
  const changeAmount = (event) => {
    setAmount(event.target.this);
  };
  const changeDate = (event) => {
    setDate(event.target.this);
  };
  const clickHandler = (event) => {
    event.preventDefault();
    const expression = {
      title: title,
      amount: amount,
      date: new Date(date)
    };
    props.onSaveExpenxeDate(expression);
    setTitle("");
    setAmount("");
    setDate("");
  };
  return (
    <form onClick={clickHandler}>
      <div className="new-expense__controls">
        <div className="new-expense__control">
          <label>Title</label>
          <input type="text" value={title} onChange={changeTitle} />
        </div>
        <div className="new-expense__control">
          <label>Amount</label>
          <input
            type="number"
            min="0.01"
            step="0.01"
            value={amount}
            onChange={changeAmount}
          />
        </div>
        <div className="new-expense__control">
          <label>Date</label>
          <input
            type="date"
            min="2019-01-01"
            max="2021-01-01 "
            value={date}
            onChange={changeDate}
          />
        </div>
      </div>
      <div className="new-expense__actions">
        <button type="submit">Add Expenses</button>
      </div>
    </form>
  );
}

export default ExpenxeForm;
